.background_titel_article{
    position: absolute;
    width: 100%;
    height: 100%; /* apply the blur */
    pointer-events: none; /* make the overlay click-through */
    background-image: url("../media/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.background_titel_article::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(10px); /* apply the blur */
    pointer-events: none; /* make the overlay click-through */
}

  
  .title_article{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .title_frame{
    position: relative;
    height: 10rem;
    width: 30rem;
    color: white;
  }
  
  .title_text{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    border: 0.5rem white solid;
    bottom: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    font-weight: bold;
  }
  
  .title_underscore{
    position: absolute;
    height: 2rem;
    left: 0px;
    right: 0px;
    bottom: 0em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }