:root{
  --header-height: 100px;
}

header{
  position: fixed;
  transform: translate(0, -100%);
  top: 0px;
  left: 0px;
  right: 0px;
  height: var(--header-height);
  background-color: red;
}

main{
  margin: -8px;
  position: relative;
  top: 0px;
  left: 0px;
  right: 0px;
  height: auto;
}

article{
  position: relative;
  width: 100%;
  min-height: 200px;
}

footer{
  position: fixed;
  transform: translate(0, 100%);
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: var(--header-height);
  background-color: rgba(255, 0, 170, 0.2);
}

nav{
  position: fixed;
  transform: translate(-100%, 0);
  bottom: 0px;
  left: 0px;
  top: 0px;
  width: 200px;
  background-color: rgb(255, 255, 255);
}

header, footer, nav{
  transition: transform 300ms;
}
